@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap");
html {
  font-size: 16px;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
p {
  margin: 0;
  padding: 0;
}

body {
  overflow-x: hidden;
}

a {
  text-decoration: none;
  color: black;
}

ul {
  list-style-type: none;
}

button {
  background: none;
  border: none;
}

.flex {
  display: flex;
}
.flex-jc-ctr {
  justify-content: center;
}
.flex-jc-sb {
  justify-content: space-between;
}
.flex-jc-sa {
  justify-content: space-around;
}
.flex-ai-ctr {
  align-items: center;
}
.flex-fd-col {
  flex-direction: column;
}

.grid {
  display: grid;
}
.grid-jc-ctr {
  justify-content: center;
}
.grid-pi-ctr {
  place-items: center;
}

html {
  font-family: "Poppins", sans-serif;
}

header {
  position: fixed;
  width: 100vw;
  top: 0;
  height: 100px;
  padding: 0 120px;
  z-index: 2;
  background-color: white;
}
header .logo_img {
  height: 55px;
}
header nav ul {
  width: 800px;
}
header nav ul li {
  display: flex;
  align-items: center;
  height: 100px;
}
header nav ul li a:hover {
  color: crimson;
}
header .logIn {
  gap: 0.6em;
}
header .logIn button {
  border: 1px solid black;
  padding: 4px 15px;
  border-radius: 50px;
  font-size: 0.8rem;
  cursor: pointer;
}
header .logIn button:hover {
  background-color: rgb(213, 213, 213);
}
header .logIn button:nth-of-type(2) {
  background-color: black;
  color: white;
}
header .logIn button:nth-of-type(2):hover {
  background-color: rgb(213, 213, 213);
  color: black;
}

.dropdown {
  position: relative;
}
.dropdown .dropdown-element {
  position: absolute;
  top: 100px;
  width: max-content;
  padding: 18px 18px;
  gap: 1.2em;
  border-top: 1px solid black;
  border-radius: 0 0 5px 5px;
  display: none;
  z-index: 2;
  background-color: white;
}
.dropdown:hover .dropdown-element {
  display: flex;
}

.hero-slider {
  width: 100vw;
  height: 680px;
  z-index: 1;
  margin-top: 100px;
}

.slideshow-container {
  width: 100%;
  position: absolute;
  margin: auto;
}

.mySlides {
  width: 100%;
  height: 680px;
  position: relative;
  overflow: hidden;
}
.mySlides img {
  position: absolute;
  top: -25%;
  filter: brightness(0.6);
}
.mySlides .text {
  color: white;
  font-size: 5rem;
  font-weight: 600;
  padding: 8px 12px;
  position: absolute;
  top: 40%;
  width: 100%;
  text-align: center;
}

.dots {
  text-align: center;
  width: 100%;
  position: absolute;
  bottom: 20px;
}

.dot {
  height: 15px;
  width: 15px;
  margin: 0 2px;
  background-color: black;
  border-radius: 50%;
  display: inline-block;
  transition: background-color 500ms ease-in-out;
  transition: transform 500ms ease-in-out;
}

.active {
  background-color: white;
  transform: scale(1.2);
}

.fade {
  animation: fade 4s ease-in 100ms iteration-count 200ms;
}

@keyframes fade {
  from {
    opacity: 0.5;
  }
  to {
    opacity: 1;
  }
}
.infograph {
  padding: 30px 180px;
  gap: 1em;
  width: 100vw;
}
.infograph-card {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.8em;
  padding: 10px 20px;
  border-right: 1px solid black;
}
.infograph-card:nth-of-type(6) {
  border: none;
}
.infograph-card img {
  height: 35px;
}
.infograph-card .infograph-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  line-height: 1.2;
}
.infograph-card .infograph-text h4 {
  font-size: 1.2rem;
  font-weight: 600;
}
.infograph-card .infograph-text p {
  font-size: 0.95rem;
}

.projects-section {
  width: 100vw;
  padding: 50px 120px;
  gap: 2em;
  background-color: rgb(232, 232, 232);
}
.projects-section h3 {
  margin-bottom: -80px;
  font-size: 2rem;
}
.projects-section .projects-section-container {
  margin: 0 auto;
  width: 100%;
  position: relative;
  height: 500px;
  padding-left: 1.2em;
}
.projects-section .projects-section-container .projects-cards {
  display: flex;
  align-items: center;
  height: 100%;
  gap: 1.2em;
}
.projects-section .projects-section-container .projects-cards .card-container {
  min-width: calc(33.33% - 1em);
  height: 300px;
  position: relative;
  border-radius: 10px;
  overflow: hidden;
  background-color: white;
}
.projects-section .projects-section-container .projects-cards .card-container .card-img-container {
  height: 80%;
  width: 100%;
  aspect-ratio: 1/1;
  position: relative;
  overflow: hidden;
}
.projects-section .projects-section-container .projects-cards .card-container .card-img-container img {
  position: relative;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.projects-section .projects-section-container .projects-cards .card-container .card-project-title {
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 30px;
}
.projects-section .projects-section-container .projects-cards .card-container .card-project-title h4 {
  font-size: 1.2rem;
  font-weight: 500;
}
.projects-section .projects-section-container .projects-cards .card-container .card-project-title .project-page {
  cursor: pointer;
  border: 1px solid black;
  border-radius: 5px;
  padding: 5px 15px;
  text-transform: uppercase;
}
.projects-section .projects-section-container .projects-cards .card-container .card-project-title .project-page:hover {
  background-color: black;
  color: white;
}
.projects-section .projects-section-container .slide-controls {
  position: absolute;
  display: flex;
  bottom: -10px;
  gap: 5em;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.projects-section .projects-section-container #next-btn,
.projects-section .projects-section-container #prev-btn {
  cursor: pointer;
  background: transparent;
  font-size: 30px;
  padding: 10px;
}
.projects-section .projects-section-container #next-btn img,
.projects-section .projects-section-container #prev-btn img {
  width: 30px;
}
.projects-section .projects-section-container #next-btn:focus,
.projects-section .projects-section-container #prev-btn:focus {
  outline: none;
}

.board-members-main {
  width: 100vw;
  padding: 50px 100px 100px 100px;
  gap: 2em;
  height: fit-content;
}
.board-members-main h3 {
  font-size: 2rem;
}
.board-members-main-container {
  gap: 1em;
}
.board-members-main-container .members-card {
  width: 300px;
  border-radius: 10px;
  overflow: hidden;
  background-color: white;
  box-shadow: 0px 6px 10px 0px rgba(0, 0, 0, 0.2);
}
.board-members-main-container .members-card .members-photo-container {
  width: 100%;
  aspect-ratio: 1/1;
  overflow: hidden;
  position: relative;
}
.board-members-main-container .members-card .members-photo-container img {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.board-members-main-container .members-card .members-title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 15px;
}
.board-members-main-container .members-card .members-title .members-text {
  line-height: 1.2;
}
.board-members-main-container .members-card .members-title .members-text h4 {
  font-size: 1.5rem;
  font-weight: 500;
}
.board-members-main-container .members-card .members-title button {
  height: fit-content;
  font-size: 0.8em;
  cursor: pointer;
  border: 1px solid black;
  border-radius: 5px;
  padding: 5px 10px;
  text-transform: uppercase;
}
.board-members-main-container .members-card .members-title button:hover {
  background-color: black;
  color: white;
}

.photo-gallery-main {
  background-color: rgb(232, 232, 232);
  border-bottom: 1px solid black;
}
.photo-gallery-main .photos-search {
  padding: 40px 120px;
  gap: 10em;
}
.photo-gallery-main .photos-search h4 {
  font-size: 2rem;
}
.photo-gallery-main .photos-search form label {
  margin-right: 20px;
  font-size: 1.5rem;
}
.photo-gallery-main .photos-search form select {
  margin-right: 10px;
  font-size: 1rem;
  padding: 10px 20px;
}
.photo-gallery-main .photos-search form input {
  font-size: 1rem;
  padding: 10px 20px;
}

.image-gallery {
  grid-template-columns: auto auto auto;
  gap: 1em;
  padding: 60px 0;
  justify-content: center;
  width: 100vw;
  background-color: rgb(232, 232, 232);
}
.image-gallery .image-card {
  width: 400px;
  height: 300px;
  border-radius: 10px;
  position: relative;
  overflow: hidden;
}
.image-gallery .image-card img {
  margin: auto;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

footer {
  width: 100vw;
  padding: 0 120px;
  height: auto;
}
footer .footer-container {
  margin: 40px 0;
}
footer .footer-container .footer-left img {
  height: 60px;
}